:root {
  --lightRed: rgb(200, 70, 70);
  --crimson: rgb(253, 81, 129);
  --blue: rgb(0, 110, 220);
  --lightBlue: rgb(17, 162, 255);
  --lightGreen: rgb(125, 180, 0);
  --teal: rgb(0, 200, 200);
  --bg-lightRed: rgb(200, 70, 70, 0.7);
  --bg-lightBlue: rgb(17, 162, 255, 0.7);
  --bg-violet: rgb(60, 20, 135, 0.7);
  --bg-lightGreen: rgb(125, 180, 0, 0.7);
  --bg-deepOrange: rgb(250, 100, 0, 0.7);
  --bg-aqua: rgb(0, 150, 150, 0.7);
  --bg-teal: rgb(0, 200, 200, 0.7);
  --bg-red: rgb(244, 32, 32, 0.7);

  --chatColor: #37474f;
  --sideMenuColor: #253238;
  --topBarColor: #fff;

  --primaryColor: #0054fe;
  --secondaryColor: #0054fe;

  --primaryText: #1a252a;
  --secondaryText: #1a252a;
  --primaryAltText: #fff;

  --primaryIconColor: #607d8b;
  --primaryGrayColor: #cfd8dc;

  --primaryBg: #fff;
  --secondaryBg: #eef1f5;
  --tertiaryBg: #ecf5fb;

  --contrastBg: #fff;
  --altContrastBg: #eef1f5;

  --primaryBtnBgColor: #deebff;
  --secondaryBtnBgColor: #deebff;

  --disabledBtnBgColor: #f2f3f4;
  --disabledBtnTextColor: #78909c;

  --gridEvenRowBg: #fff;
  --gridOddRowBg: #f8f8f8;
  --gridFocusRowBg: #b4e1ff;
  --gridHighlightRowBg: #ecf5fb;
  --gridRowBorder: #eceff1;
