  --disabledInputBorder: #e4e9eb;
  --disabledInputBg: #f7f9f9;

  --lightLabel: #607d8b;
  --formLabel: #1a252a;
  --contrastGray: #cfd8dc;

  --shadowColor: rgb(0, 84, 253, 0.5);
  --overlayBg: rgba(255, 255, 255, 0.5);
  --redColor: rgba(193, 1, 0, 0.7);

  --graphColor1: #e74d33;
