  --graphColor9: #78c078;
  --graphColor10: #989ca0;
  --graphColor11: #fb924c;
  --graphColor12: #ff87d9;
}

::-moz-selection {
  background: var(--primaryColor);
  color: var(--primaryBg);
}

::selection {
  background: var(--primaryColor);
  color: var(--primaryBg);
}

body,
html {
  width: 100%;
  background-color: var(--secondaryBg);
  font-size: $baseFontSize;
  color: var(--primaryText);
  scroll-behavior: smooth;

  a {
    color: var(--secondaryColor);
    text-decoration: none;

    &:hover {
      color: var(--primaryColor);
    }

    &:active {
      color: var(--primaryColor);
    }
  }
}

body {
  overflow-x: hidden;
}

.lightMode {

  .no-records-found-img,
  .grid-no-records-found-img {
    background-image: url(assets/images/norecordsfound.svg);
  }

  .no-records-found-chart {
    background-image: url(assets/chart.png);
  }

  .no-criteria-found {
    background-image: url("assets/images/no_search_cretiera.svg");
  }

  .card-container .no-records-found {
    background-image: url(assets/images/norecordsfound.svg);
  }
}

.darkMode {

  .new-item,
  .new-feature-bg {
    // filter: brightness(0.9);

    &.new-feature-1-bg .new-item-text {
      color: #00746d;
    }

    &.new-feature-2-bg .new-item-text {
      color: #724700;
    }

    &.new-feature-3-bg .new-item-text {
      color: #7e6001;
    }
  }

  .no-records-found-img,
  .grid-no-records-found-img {
    background-image: url(assets/images/norecordsfound_dark.svg);
    background-size: 70% 70%;
  }

  .no-records-found-chart {
    background-image: url(assets/chart_dark.svg);
    background-size: 65% 65%;
  }

  .no-criteria-found {
    background-image: url("assets/images/no_search_cretiera_dark.svg");
    background-size: 65% 65%;
  }

  .card-container .no-records-found {
    background-image: url(assets/images/norecordsfound_dark.svg);
    background-size: 200px 200px;
  }

  .cdk-drag-preview {
    &.drag-items {
      background-color: #000 !important;
      border: 1px solid #fff;
    }

    &.drag .card {
      background-color: #000 !important;
      border-color: var(--primaryColor);
    }
  }

  .display-feature {
    background-color: #000;
  }
}

